.app-header {
    display: flex;
    justify-content: space-between;
    font-family: Rubik-regular, sans-serif;
    .header-container {
        display: flex;
        flex-direction: column;
        direction: rtl;
        .header-content {
            margin-bottom: rem(20px);
            h1 {
                font-size: rem(60px);
                color: #000;
                margin-bottom: 10px;
            }
            h4 {
                font-size: rem(25px);
                // line-height: 20px;
                font-family: Rubik-bold, sans-serif;
                color: #000;
                margin: 0;
            }
            p {
                direction: rtl;
                line-height: rem(30px);
                margin-top: 10px;
                font-size: rem(20px);
            }
        }
        .header-buttons {
            border: none;
            background-color: $clr7;
            width: rem(199px);
            height: rem(40px);
            cursor: pointer;
            border-radius: 2px 15px;
            text-decoration: none;
            span{
                font-family: Rubik-medium, sans-serif;
                font-size: rem(18px);
                color: $clr6;
            }
        }
    }
    .header-buttons {
      border: none;
      background-color: $clr7;
      width: rem(199px);
      height: rem(40px);
      cursor: pointer;
      border-radius: 2px 15px;
      text-decoration: none;
      span {
        font-family: Rubik-medium, sans-serif;
        font-size: rem(18px);
        color: $clr6;
      }
    }
  }


/////////////////////// media Query max widh 480 ////////////////////////////

@media only screen and (max-width: 480px) {
 
  .app-header {
    display: flex;
    flex-direction: column;
    padding: 0;
    margin: 0;
  }
  .header-comp {
    padding: 0;
    margin: 0;
  }
}

