.label_div {
  h1 {
    float: right;
    margin: 2.5rem;
  }
}
.search-job-by-buttons {
  width: 94.5%;
}
.sorting-jobs {
  display: flex;
  justify-content: center;
  margin-bottom: 1rem;
  direction: rtl;

  select {
    float: right;
    margin: 0.5rem;
    border-radius: 5px;
    height: 2.4rem;
    width: 10rem;
  }
  input {
    float: left;
    border-radius: 5px;
    width: 15rem;
    height: 2rem;
    margin: 0.5rem;
  }
}

.jobs-page {
  height: 700px;
  background-color: white;
  margin-bottom: 50px;

  ::-webkit-scrollbar {
    display: none;
  }

  .description-job {
    border: 1px solid #34018e;
    width: 50%;
    border-radius: 10px;
    direction: rtl;
    padding: 2% 4%;

    .container-job {
      height: 100%;
      justify-content: space-between;

      .social {
        .icon-social {
          img {
            width: 46px;
          }
        }
      }

      .buttons-modify {
        width: 100%;
        align-items: flex-end;
        justify-content: end;
        gap: 10px;

        span {
          display: inline;
          background-color: #f2f2f2;
          text-align: center;
          height: 25%;
          padding: 3px 15px 3px 30px;

          img {
            margin-left: 10px;
            vertical-align: middle;
          }
        }
      }
    }

    .card-content-info {
      gap: 20px;

      span {
        display: flex;
        justify-content: center;
        align-items: center;
      }

      .job-icon {
        width: 30px;
        font-size: 18px;
        color: #34018e;
        margin-left: 10px;
        position: relative;
        top: -4px;
      }
    }
  }

  .jobs-list {
    border-top: 1px solid #34018e;
    border-bottom: 1px solid #34018e;
    border-radius: 10px;
    width: 35%;
    overflow-y: auto;

    .job-card {
      border-radius: 10px;
      direction: rtl;
      width: 100%;
      height: 120px;
      border: 1px solid #34018e;
      padding: 5px;
      box-sizing: border-box;
      cursor: pointer;
    }

    #job-card-clicked {
      background-color: #6ce5e7;
    }
  }
}

