.title {
  font-family: Rubik;
  font-style: normal;
  font-weight: bold;
  font-size: 30px;
  line-height: 36px;
  text-align: center;

  color: #4a4a4a;
}
.alertCard {
  background: #efd9db;
  padding: 5px;
  width: 90%;
  left: 4%;
  position: relative;
}
.alertText {
  margin: 10px;
  font-style: normal;
  font-weight: bold;
  font-size: 14px;
  line-height: 21px;
  text-align: right;
  color: #9d3332;
}
.confirmText {
  font-style: normal;
  font-weight: 500;
  font-size: 18px;
  line-height: 21px;
  text-align: right;

  color: #4a4a4a;
}
